.my-navbar-item {
  color: #999;
}
.my-navbar-item:hover {
  color: #ffffff !important;
  background: #161616;
  transition: all 0.3s ease;
}
.my-navbar-item a:hover {
  color: #ffffff !important;
}
.my-navbar-item.active {
  color: #cca354;
  background: #222;
  transition: all 0.3s ease;
}
.my-navbar {
  transition: width 0.2s ease-in-out; /* Set the transition for smooth animation */
  width: 0%;
  overflow: hidden;
}

.login_bg {
  background: url("/assets/images/bg/forex_bg_1920_ Ã—_ 832 px.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.notification {
  position: fixed;
  top: 20px;
  right: 20px;
  background-color: #000;
  color: #fff;
  border: 1px solid rgba(204, 163, 84, 0.45); /* Golden border */
  border-radius: 5px;
  width: 80%;
  z-index: 99999;
}
@media screen and (min-width: 576px) {
  .notification {
    width: 60%;
  }
}
.my_alert {
  background-color: #000;
  color: #fff;
  border: 1px solid rgba(204, 163, 84, 0.45); /* Golden border */
  border-radius: 5px;
  width: "100%";
  font-family: "Josefin Sans", sans-serif;
}
.my_result_container {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.my_result {
  color: #fff;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  font-family: "Josefin Sans", sans-serif;
  padding: 10px 0;
  gap: 20px;
}
.my_result_text {
  font-size: 16px;
  text-align: center;
}

.progress-container {
  height: 3px;
  background-color: #555;
  border-radius: 5px;
  margin-top: 10px;
  width: "100%";
}

.progress-bar {
  height: 100%;
  width: 0;
  background-color: #cca354; /* Golden progress bar */
  border-radius: 5px;
  animation: progressAnimation 2s linear forwards;
}

@keyframes progressAnimation {
  100% {
    width: 100%;
  }
}
@keyframes glowing {
  0% {
    box-shadow: 0 0 5px #ffd700; /* Golden glow color */
  }
  50% {
    box-shadow: 0 0 15px #ffd700; /* Increase glow */
  }
  100% {
    box-shadow: 0 0 5px #ffd700; /* Return to initial glow */
  }
}

/* Apply animation to the span element */
.glow {
  animation: glowing 1s ease-in-out infinite; /* Modify the duration as needed */
}
.spinner {
  border: 2px solid rgba(0, 0, 0, 0.1);
  border-top: 2px solid #81631c; /* Change color if needed */
  border-right: 2px solid #81631c; /* Change color if needed */
  border-radius: 50%;
  animation: spin 1s linear infinite;
  transform: translate(-50%, -50%);
  position: absolute;
  top: 50%;
  left: 10%;
}
@keyframes spin {
  0% {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  100% {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}
.hero_bg {
  background: url("/assets/images/bg/bg-1.jpg");
