}
.myTooltip {
  position: absolute;
  background-color: black;
  color: white;
  padding: 5px;
  border-radius: 5px;
  font-size: 14px;
  bottom: 120%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
}

.myTooltip::before {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent #000 transparent;
  transform: rotate(180deg);
}
